ties for open(-source) collaboration between professionals and activists. What remains the same is our doer culture —we never stop developing new tools, exploring new models of organization, experimenting on new forms of media, and creating new relationships in and through hackathons.")])]),_c('h3',[_vm._v("g0v Summit 2018")]),_c('p',[_vm._v("g0v Summit is the largest Civic Tech and hackers’ conference in Asia. This year, our theme is “ecosystem”. Please join us to examine the ecosystem of Civic Tech in the past, present and the future, both at the global and local scales. What have been grown? What have been connected? What have been changed? What have been accumulated? 2018 is also witnessing many elections around the world, including local elections in Taiwan. How will these elections affect local and regional politics? What impacts will open government and Civic Tech have on elections?")]),_c('p',[_vm._v("As a hackers’ community, we—g0v contributors—believe in the power of doing. We value ongoing or completed projects, while recognizing at the same time that failure, new and fragile ideas, and stories are equally important. Therefore, we have curated two categories of topics—“Civic Tech Projects On Stage” and “Behind the Scenes”—for you to choose from. Through this categorization, along with a more detailed description of the themes included in each topic, we hope that each project can receive better feedback and we wish to give each story an opportunity to be shared and reflected.")]),_c('h3',[_vm._v("Calling for proposals now")]),_c('p',[_vm._v("Like our vibrant community, g0v Summit is great thanks to contributors’ efforts. This year, we strive to make the Summit an open platform for sharing and brainstorming ideas and projects. We hope that engineers and researchers, activists and creators, keep changing our society through technology, expertise, and the power of collaboration. If you have something to share that fits the topics below, we sincerely invite you to submit your proposal and to become a contributor.")])]),_c('div',{staticClass:"text-center"},[_c('CtaButton',{attrs:{"href":"https://docs.google.com/forms/d/e/1FAIpQLSeESRbF_tmRUzaAFdEcTl5vhor7thL3ahpiR3wDAJaSVUXFhw/viewform","block":"block"}},[_c('TW',[_vm._v("馬上投稿（已截止）")]),_c('EN',[_vm._v("Submit a proposal（closed）")])],1)],1)],1),_c('div',{staticClass:"cell large-12 large-order-3"},[_c('h3',[_c('TW',[_vm._v("我們關注的主題")]),_c('EN',[_vm._v("Topics")])],1),_c('div',{staticClass:"grid-x grid-padding-x"},[_c('div',{staticClass:"topicsGroup cell large-7"},[_c('h4',[_vm._v("公民科技台上 / Civic Tech Project On Stage")]),_c('div',{staticClass:"grid-y"},_vm._l((_vm.topicsOnStage),function(topics,category){return _c('div',{staticClass:"cell"},[_c('div',{staticClass:"card"},[_c('div',{staticClass:"card-section"},[_c('h5',{staticClass:"category"},[_vm._v(_vm._s(category))])]),_vm._l((topics),function(topic){return _c('div',{staticClass:"card-section"},[_c('p',{staticClass:"topic"},[_vm._v(_vm._s(topic))])])})],2)])}))]),_c('div',{staticClass:"topicsGroup cell large-5"},[_c('h4',[_vm._v("公民科技台下 / Behind the Scenes")]),_c('div',{staticClass:"grid-y"},_vm._l((_vm.topicsBehindScene),function(topics,category){return _c('div',{staticClass:"cell"},[_c('div',{staticClass:"card"},[_c('div',{staticClass:"card-section"},[_c('h5',{staticClass:"category"},[_vm._v(_vm._s(category))])]),_vm._l((topics),function(topic){return _c('div',{staticClass:"card-section"},[_c('p',{staticClass:"topic"},[_vm._v(_vm._s(topic))])])})],2)])}))])])]),_c('div',{staticClass:"cell large-4"},[_c('TW',[_c('h3',[_vm._v("徵件形式")]),_c('p',[_vm._v("徵件形式分為下列兩種")]),_c('ul',[_c('li',[_vm._v("30 分鐘的專案展示或短講")]),_c('li',[_vm._v("60 或 90 分鐘的開放討論或工作坊")])]),_c('h3',[_vm._v("徵件時間及注意事項")]),_c('p',[_vm._v("徵件即日開始，於台灣時間 2018 年 6 月 30 日晚間 11 時 59 分截止。錄取結果將於七月下旬公佈。")]),_c('p',[_vm._v("我們將優先接受專案成果、專案影響力、知識分享的投稿。")]),_c('p',[_vm._v("我們將注重東亞、台灣的在地特性，並且努力朝向性別、地域平等。")]),_c('h3',[_vm._v("旅費補助")]),_c('p',[_vm._v("居住在台灣以外的講者，可向大會申請旅費補助與住宿。如